body {
  font-size: 14px;
  letter-spacing: 0.03em;
  margin: 0;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  outline: none;
}

h1,
h2,
h4,
h5 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-bottom: 0;
  margin-top: 0;
}

ul {
  padding: 0;
  margin-top: 0;
  margin: 0;
  margin-bottom: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
.container {
  box-sizing: content-box;

  margin-left: auto;
  margin-right: auto;
  padding-right: 16px;
  padding-bottom: 20px;
  padding-left: 16px;
  background-image: url(../../assets/img/landing_page_template_with_flowing_lines_design_2805.jpg);
  background-size: cover, cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
}
